.cardImageLight {
    fill: #2c3e50;
    z-index: 2;
    position: absolute;
    left: 5px;
    height: 100px;
    width: 100px;
    top: calc((100% - 100px) / 2);
    left: 20px;
}

.cardImageDark {
    fill: #cccccc;
    z-index: 2;
    position: absolute;
    left: 5px;
    height: 100px;
    width: 100px;
    top: calc((100% - 100px) / 2);
    left: 20px;
}